import axios from 'axios';
import { WeatherData, DashboardFilters } from '../types/weather';

const api = axios.create({
  headers: {
    'Content-Type': 'application/json',
  },
});

export const weatherApi = {
  // Fetch weather data with filters
  getWeatherData: async (filters: DashboardFilters): Promise<WeatherData[]> => {
    try {
      const params = new URLSearchParams();
      
      if (filters.location) {
        params.append('location', filters.location);
      }
      
      if (filters.startDate) {
        params.append('startDate', filters.startDate);
      }
      
      if (filters.endDate) {
        params.append('endDate', filters.endDate);
      }
      
      if (filters.timeRange) {
        params.append('timeRange', filters.timeRange);
      }

      const response = await api.get(`/api/weather/data?${params.toString()}`);
      return response.data;
    } catch (error) {
      console.error('Error fetching weather data:', error);
      throw error;
    }
  },

  // Get unique locations from the data
  getLocations: async (): Promise<string[]> => {
    try {
      const response = await api.get('/api/weather/locations');
      return response.data;
    } catch (error) {
      console.error('Error fetching locations:', error);
      throw error;
    }
  }
};

export default api; 